.popup__input {
  width: 358px;
  border: none;
  margin: 0;
  padding: 0;
  margin-top: 18px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 11px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}


@media screen and (min-width: 320px) and (max-width: 767px) {
  .popup__input {
    width: 238px;
    padding-bottom: 9px;
  }
}

